@import "variables";

.featured-product {

	&__heading {
		margin: auto;	
		text-align: center;
	}

	&__flex-container {
		
		@include media-lg {
			display: flex;
			justify-content: center;
			flex-direction: row;
		}
	}

	&__image-box {
		width: 100%;	
		margin-bottom: 42px;

		@include media-lg {
			width: 50%;	
		}
	}

	&__grouping {

		@include media-lg {
			align-self: center;	
		}	
	}
	&__button {
		margin: auto;	
	}
}

.product-info-container {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	margin-bottom: 40px;

	@include media-sm {
		justify-content: center;	
	}

	@include media-lg {
		display: block;	
	}
}

.product-info {
		width: 33%;

		@include media-sm {
			width: auto;
			padding-left: 60px;
			padding-right: 60px;
		}

		@include media-lg {
			padding-bottom: 40px;	
		}

	&__icon {
		background-origin: border-box;
		background-position: center;
		background-repeat: no-repeat;
		background-size: contain;
		height: 49px;	
		margin: auto;
		margin-bottom: 23px;
		width: 49px;


		&--cup {
			@extend .product-info__icon;
			background-image: url(/images/icons/icon_coffee_cup-1.png);
		}

		&--shovel {
			@extend .product-info__icon;
			background-image: url(/images/icons/icon_coffee_shovel.png);
		}

		&--plant {
			@extend .product-info__icon;
			background-image: url(/images/icons/icon_coffee_plant.png);	
		}
	}

	&__text {
		text-align: center;
	}
}
