@import "variables";

.g-map {
  padding-top: 75%;
  position: relative;
  width: 100%;

  &__heading {
    padding-bottom: 35px;
    padding-left: $padding-container-m;
  }

  &__iframe {
    border: none;
    display: block;
    height: 100%;
    left: 0;
    overflow: hidden;
    position: absolute;
    top: 0;
    width: 100%;
  }
}
